@font-face {
  font-family: "Noto Sans Georgian";
  src: local("Noto Sans Georgian"), url(./fonts/NotoSansGeorgian-Regular.ttf) format("truetype"); }

@font-face {
  font-family: "Noto Sans Georgian";
  font-weight: 600;
  src: local("Noto Sans Georgian"), url(./fonts/NotoSansGeorgian-SemiBold.ttf) format("truetype"); }

@font-face {
  font-family: "Noto Sans Georgian";
  font-weight: 700;
  src: local("Noto Sans Georgian"), url(./fonts/NotoSansGeorgian-Bold.ttf) format("truetype"); }

@font-face {
  font-family: "Noto Sans Georgian";
  font-weight: 800;
  src: local("Noto Sans Georgian"), url(./fonts/NotoSansGeorgian-ExtraBold.ttf) format("truetype"); }

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  font-size: 62.5%;
  scroll-behavior: smooth; }

body {
  font-family: "Noto Sans Georgian", sans-serif;
  font-size: 1.6rem; }

img {
  display: block;
  width: 100%; }

ul {
  list-style: none; }

a {
  text-decoration: none; }

.btn {
  outline: none;
  border: none;
  cursor: pointer; }

.container {
  max-width: 140rem;
  margin: 0 auto;
  padding: 0 4rem; }

.heading-primary {
  color: #fff; }

.heading-secondary {
  font-size: 3.5rem;
  position: relative;
  vertical-align: middle; }
  .heading-secondary::after {
    content: "";
    position: absolute;
    left: 50%;
    width: 15%;
    height: 1px;
    background-color: #449440;
    transform: translateX(-50%); }
  .heading-secondary::after {
    bottom: -30px; }

.heading-tertiary {
  color: #449440; }

.btn-primary {
  padding: 1rem 3rem;
  border-radius: 0.2rem;
  background-color: #449440;
  color: #fff;
  opacity: 0.7;
  transition: all 0.2s;
  font-weight: 600; }
  .btn-primary:hover {
    opacity: 1; }

.btn-secondary {
  padding: 1rem 3rem;
  border-radius: 0.2rem;
  background-color: rgba(255, 255, 255, 0.85);
  position: absolute;
  color: #000;
  left: 50%;
  transform: translateX(-50%);
  font-weight: 600;
  font-size: 1.4rem;
  width: 15rem;
  margin-top: 2rem;
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s; }
  .btn-secondary:hover {
    background-color: white; }
  @media only screen and (max-width: 75em) {
    .btn-secondary {
      visibility: visible;
      opacity: 1; } }
  @media only screen and (max-width: 37.5em) {
    .btn-secondary {
      margin-top: 1rem; } }

.btn-tertiary {
  background-color: transparent; }
  .btn-tertiary svg {
    transition: all 0.2s; }
  .btn-tertiary:hover svg {
    fill: #449440; }

.btn-navigation {
  padding: 0.6rem 0.3rem;
  background-color: transparent;
  z-index: 1100; }
  .btn-navigation > *:not(:last-child) {
    margin-bottom: 0.3rem; }

.btn-span {
  width: 2rem;
  background-color: #fff;
  height: 2px;
  display: block;
  transition: all 0.2s; }

.popup {
  visibility: hidden;
  opacity: 0;
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  color: #000;
  background-color: rgba(0, 0, 0, 0.5);
  transition: all 0.3s;
  z-index: 9999; }
  .popup__content {
    opacity: 0;
    visibility: hidden;
    display: flex;
    flex-direction: column;
    width: 75%;
    height: 50rem;
    background-color: #fff;
    padding: 4rem;
    text-align: center;
    box-shadow: 0 2rem 4rem rgba(0, 0, 0, 0.2);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0.5);
    transition: all 0.2s 0.3s;
    overflow: scroll; }
    @media (max-width: 37.5em) {
      .popup__content {
        padding: 2rem;
        width: 90%;
        font-size: 1.4rem; } }
  .popup__button-container {
    align-self: flex-end; }
  .popup__text-container {
    margin-top: auto;
    margin-bottom: auto; }
  .popup__heading-container {
    margin-bottom: 4rem; }

.header {
  height: 100vh;
  position: relative;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(./img/hero-1.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed; }
  .header__top {
    border-bottom: 2px solid #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2rem 0; }
  .header__logo-container {
    flex-basis: 10%; }
  .header__logo-img {
    width: 35%; }
  @media only screen and (max-width: 37.5em) {
    .header__nav {
      position: fixed;
      top: 0;
      left: 100%;
      height: 100vh;
      width: 100%;
      text-align: center;
      background-color: #449440;
      transition: all 0.3s ease-in-out;
      z-index: 1000;
      font-size: 3rem; } }
  .header__nav-button-container {
    display: none; }
    @media (max-width: 37.5em) {
      .header__nav-button-container {
        display: block; } }
  .header__list {
    display: flex; }
    .header__list > *:not(:last-child) {
      margin-right: 2rem; }
    @media only screen and (max-width: 37.5em) {
      .header__list {
        flex-direction: column;
        margin-top: 20rem; }
        .header__list > *:not(:last-child) {
          margin-right: 0;
          margin-bottom: 2rem; } }
  @media (max-width: 37.5em) {
    .header__list-item {
      display: block; }
      .header__list-item:first-child {
        animation-delay: 0.2s; }
      .header__list-item:nth-child(2) {
        animation-delay: 0.4s; }
      .header__list-item:nth-child(3) {
        animation-delay: 0.6s; }
      .header__list-item:last-child {
        animation-delay: 0.8s; } }
  .header__link {
    color: #fff;
    position: relative; }
    .header__link:hover::after {
      width: 100%; }
    .header__link::after {
      content: "";
      bottom: -5px;
      left: 0;
      height: 2px;
      width: 0;
      position: absolute;
      background-color: #449440;
      transition: all 0.5s; }
  .header__bottom {
    margin-top: auto;
    text-align: center;
    margin-bottom: auto; }
  .header__heading-container {
    margin-bottom: 5rem; }
  .header__button-container {
    text-align: center; }

.section-about {
  padding: 15rem 0; }
  .section-about__heading-container {
    text-align: center;
    margin-bottom: 15rem; }
  .section-about__info {
    display: flex;
    justify-content: space-between; }
    @media only screen and (max-width: 75em) {
      .section-about__info {
        justify-content: space-evenly; } }
    @media only screen and (max-width: 50em) {
      .section-about__info {
        flex-direction: column; }
        .section-about__info > *:not(:last-child) {
          margin-bottom: 4rem; } }
  .section-about__info-box {
    flex-basis: 43%;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-radius: 0.2rem;
    padding: 3rem; }
    @media only screen and (max-width: 50em) {
      .section-about__info-box {
        text-align: center; } }
  .section-about__icon-container {
    margin-bottom: 1.5rem; }
    .section-about__icon-container svg {
      fill: #449440; }

.section-services {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(./img/fence-3.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  color: #fff;
  padding: 12rem 0; }
  @media (max-width: 37.5em) {
    .section-services {
      padding: 8rem 0; } }
  .section-services__heading-container {
    text-align: center;
    margin-bottom: 10rem; }
  .section-services__services {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 5rem;
    padding-bottom: 8rem; }
  .section-services__service {
    border: 2px solid #fff;
    flex-basis: 30%;
    position: relative;
    overflow: hidden;
    text-align: center;
    transition: all 0.2s; }
    @media only screen and (max-width: 75em) {
      .section-services__service {
        flex-basis: 40%; } }
    @media only screen and (max-width: 50em) {
      .section-services__service {
        flex-basis: 80%; } }
    @media only screen and (max-width: 37.5em) {
      .section-services__service {
        flex-basis: 100%; } }
    @media only screen and (min-width: 50em) {
      .section-services__service:hover img {
        filter: brightness(100%);
        transform: scale(1.05); }
      .section-services__service:hover figcaption {
        background-color: rgba(0, 0, 0, 0.5); }
      .section-services__service:hover .btn-secondary {
        opacity: 1;
        visibility: visible; } }
  .section-services__service-img {
    filter: brightness(50%);
    backface-visibility: hidden;
    transition: all 0.2s; }
    @media only screen and (max-width: 75em) {
      .section-services__service-img {
        filter: brightness(100%); } }
  .section-services__figcaption {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.2s;
    font-weight: 600;
    padding: 0.5rem;
    width: 85%; }
    @media only screen and (max-width: 75em) {
      .section-services__figcaption {
        background-color: rgba(0, 0, 0, 0.5); } }
    @media only screen and (max-width: 37.5em) {
      .section-services__figcaption {
        transform: translate(-50%, -70%); } }

.footer {
  padding: 6rem 0;
  background-color: #eee; }
  @media only screen and (max-width: 43.75em) {
    .footer {
      padding-bottom: 3rem; } }
  .footer__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 2px solid #449440;
    padding: 2rem 0; }
    @media only screen and (max-width: 43.75em) {
      .footer__top {
        flex-direction: column; }
        .footer__top > *:not(:last-child) {
          margin-bottom: 2rem; } }
  .footer__logo-container {
    flex-basis: 5%; }
    @media only screen and (max-width: 43.75em) {
      .footer__logo-container {
        width: 20%; } }
  .footer__logo-img {
    width: 50%;
    margin: 0 auto; }
  .footer__list {
    display: flex; }
    .footer__list > *:not(:last-child) {
      margin-right: 2rem; }
    @media only screen and (max-width: 43.75em) {
      .footer__list {
        flex-direction: column;
        text-align: center; }
        .footer__list > *:not(:last-child) {
          margin-right: 0;
          margin-bottom: 1rem; } }
  .footer__link {
    color: #000;
    transition: all 0.2s; }
    .footer__link:hover {
      color: #449440; }
  .footer__social-media {
    display: flex;
    align-items: center; }
    .footer__social-media > *:not(:last-child) {
      margin-right: 2rem; }
  .footer__icon-container {
    width: 2.5rem;
    height: 2.5rem; }
    .footer__icon-container svg {
      width: 100%;
      height: 100%;
      transition: all 0.2s; }
  .footer__icon-container:first-child:hover svg {
    fill: #3b5998; }
  .footer__icon-container:last-child:hover svg {
    fill: #e1306c; }
  .footer__contact {
    display: flex;
    justify-content: center;
    align-items: center; }
    .footer__contact > * {
      display: flex;
      align-items: center; }
    .footer__contact > *:not(:last-child) {
      margin-right: 2rem; }
    @media only screen and (max-width: 37.5em) {
      .footer__contact {
        flex-direction: column; } }
  .footer__contact-icon-container {
    width: 2.5rem;
    height: 2.5rem;
    margin-right: 1rem; }
    .footer__contact-icon-container svg {
      width: 100%;
      height: 100%; }
  .footer__contact-link {
    color: #000;
    transition: all 0.2s;
    font-family: "Noto Sans", sans-serif; }
    .footer__contact-link:hover {
      color: #449440; }

.u-fl {
  display: flex;
  flex-direction: column;
  height: 100%; }

.open {
  visibility: visible;
  opacity: 1; }
  .open .popup__content {
    opacity: 1;
    visibility: visible;
    transform: translate(-50%, -50%) scale(1); }

@media (max-width: 600px) {
  .span1-active {
    transform: rotate(135deg) translate(3px, -3px); }
  .span2-deactive {
    opacity: 0; }
  .span3-active {
    transform: rotate(-135deg) translate(4px, 4px); }
  .nav-active {
    transform: translate(-100%); }
  .btn-navigation-active {
    position: fixed;
    top: 2rem;
    right: 4rem; }
  .list-item-active {
    animation-name: fadeInDown;
    animation-duration: 1s;
    animation-fill-mode: both; } }

@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translate3d(0, -100%, 0); }
  to {
    opacity: 1;
    transform: none; } }
